        id: 'The-Psychological-Mind',
        title: 'The Psychological Mind',
        body: `<p>The Chimp is an emotional machine that thinks independently from us. It is not good or bad, it is just a Chimp.</p><p> You are not responsible for the nature of your Chimp but you are responsible for managing it.</p><p> One of the secrets of success and happiness is to learn to live with your Chimp and not get bitten or attacked by it. To do this, you need to understand how your Chimp behaves, and why it thinks and acts in the way that it does. You also need to understand your Human and not muddle up your Human with your Chimp.</p><p> The Psychological Mind has two <strong>independent</strong> thinking machines that also <strong>independently</strong> interpret our experiences: Human (Frontal) and Chimp (Limbic).</p><p> You are the Human.</p><p> Your Chimp is an emotional thinking machine.</p><p> Your Computer is a storage area and automatic functioning machine.</p><p> Any one of them can take complete control but usually they work together.</p>`
