        id: 'The-Planet-of-Others',
        title: 'The Planet of Others',
        body: `<h2> Snow White Mindset</h2><p> Innocent, passive victim at the mercy of others and circumstances. Completely devoid of accountability, responsibility, and power to change the circumstances.</p><h2> Alpha Wolf Mindset</h2><p> varying degress of dictator and control freak. Success and ego are paramount.</p><p> When dealing with others,</p><ol> <li> try not to conclude from first impressions </li><li> try not to have preconceived notions of others </li><li> beware of prejudices </li></ol><p> Human searches for Truth, Fact and Evidence before reaching Conclusion.</p><p> Chimp has a COnclusion and then searches of 'facts', 'truth' and 'evidence' to support the Conclusion.</p><p> Successful people don't make demands of others, but set the scene so that the Human in other people can respond, rather than their Chimp.</p><p> It is <strong>your decision to remain w</strong>ith other people and toaccept them as they are or to leave them just as it is <strong>their right to act in the way that they want to.</strong></p><p> One in 5 people will love you regardless what you do. One in 5 people will hate you regardless what you will do. The other 3 will be balanced.</p>`
