        body: `<p> How to be happy</p><p> 3 states: positive, neutral, negative.</p><p> there are 2 lists to achieve happiness:</p><p> having list and the being list.</p><p> having list includes</p><ol> <li> achievements </li><li> posessions </li><li> emotional </li><li> physical </li><li> intellectual </li></ol><p> being list includes</p><ol> <li> self worth </li><li> self image </li><li> self confidence </li><li> self esteem </li></ol><p> focus on being list not having list.</p><p> Items on Being list is about choice.</p><p> self image is how you see your personality and presence</p><p> self worth is the value you put yourself as a person</p><p> self esteem is how you see yourself compared to others</p><p> self confidence is what you believe you are capable of doing</p><h1> The Moon of Confidence</h1><p> How to be confident</p><p> Confidence based on ability gives power to the Chimp</p><p> Confidence based on giving your best gives power to the Human</p><h1> The Moon of Security</h1><p> How to develop security</p><p> Chimp: I think there is risk!! REmove it!!!!</p><p> Human : Sometimes risk cannot be avoided. You have to accept it. How can i help my Chimp?</p><p> The Chimp frequently seeks security unrealistically. You can place an Autopilot in the Computer to steady the Chimp.</p>`
